
import React, { Component, useMemo } from 'react';
import { Logger, getSearchResult, openPageInNewTab, reloadPage, goHome, __IST__ } from '../../Utils/Utils';
import { Loading } from '../Statuses/Statuses';
import { BACKEND_GENERIC_LOGGER_SERVICE_BASE_URL } from 'src/Rest-API/EnvironmentSpecificThings';
import { getUserDataFromLocalStorage, logoutProcedureForLocalStorage } from 'src/Utils/AuthRelatedThings';
import { requestHeaderGETWithAuth, requestHeaderPUTWithAuth, requestHeaderPOSTWithAuth } from 'src/Rest-API/Header';
import {
    CButton,
    CCard,
    CCardBody,
    CCardGroup,
    CCol,
    CContainer,
    CForm,
    CInput,
    CInputGroup,
    CInputGroupPrepend,
    CInputGroupText,
    CRow,
    CTextarea
  } from '@coreui/react';
import StatusModal from '../Modals/StatusModal';
import FailureModal from '../Modals/FailureModal';
import MaterialReactTable from 'material-react-table';
import moment from 'moment';
import CIcon from '@coreui/icons-react';

//default
const FILE_NAME = "LoggerUserDefinedDataType.js";

//default
const initialState = {
    isLoaded: false,
    loadingModalVisible: false,
    loadingModalState: 0,
    failureModalVisible: false,
    userDefinedDataTypeListData: []
};

const tableColumnData = [
    {
        accessorKey: 'id',
        header: 'ID',
        enableEditing: false,
    },
    {
        accessorKey: 'name',
        header: 'Name',
        enableEditing: true,
    },
    {
        accessorKey: 'metadata',
        header: 'Metadata',
        enableEditing: false,
        Cell: ({ renderedCellValue, row }) => {
            // console.log("renderedCellValue = ", renderedCellValue);
            const elements = JSON.parse(renderedCellValue);
            // console.log("elements = ", elements.length);
            let renderedData = "";
            elements.forEach(element => {
                renderedData += ("[" + element.name + " : " + element.valueType + "] ~ ")
            });
            
            return renderedData
        }
    },
    {
        accessorKey: 'createdAt',
        header: 'Created At',
        enableEditing: false,
        Cell: ({ renderedCellValue, row }) => (moment(renderedCellValue).zone('IST').format('YYYY/MM/DD HH:mm:ss ddd') + __IST__)
    },
    {
        accessorKey: 'updatedAt',
        header: 'Updated At',
        enableEditing: false,
        Cell: ({ renderedCellValue, row }) => (moment(renderedCellValue).zone('IST').format('YYYY/MM/DD HH:mm:ss ddd') + __IST__)
    },
];


class LoggerUserDefinedDataType extends Component {

    //default
    constructor(props) {
        super(props);
        this.state = JSON.parse(JSON.stringify(initialState));
    }

    //default
    resetStateToDefault() {
        this.setState(JSON.parse(JSON.stringify(initialState)));
    }

    //default
    reloadData() {
        this.resetStateToDefault();
        this.fetchUserDefinedDataTypeDataList();
    }

    //default
    componentDidMount() {
        document.title = "Logger - User-Defined Data-Type";
        this.fetchUserDefinedDataTypeDataList();
    }

    fetchUserDefinedDataTypeDataList() {
        let API_URL = BACKEND_GENERIC_LOGGER_SERVICE_BASE_URL + '/user-defined-data-type/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchUserDefinedDataTypeDataList", "FETCH_1", "result", result);
                    //success
                    this.setState({
                        isLoaded: true,
                        userDefinedDataTypeListData: result.data
                    });
            },
            (error) => {
                Logger(FILE_NAME, "fetchUserDefinedDataTypeDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    submitCreateUserDefinedDataTypeForm = (event) => {
        event.preventDefault();

        let reqData = {
            name: event.target.inputName.value
        };

        this.setState({
            loadingModalVisible: true,
            loadingModalState: 0
        });

        this.callCreateUserDefinedDataTypeApi(reqData);
    }

    callCreateUserDefinedDataTypeApi(newUserDefinedDataType) {
        let API_URL = BACKEND_GENERIC_LOGGER_SERVICE_BASE_URL + '/user-defined-data-type/save-x?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();
        fetch(API_URL, requestHeaderPOSTWithAuth(JSON.stringify(newUserDefinedDataType), userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "callCreateUserDefinedDataTypeApi", "FETCH_1", "result", result);
                //success
                this.setState({
                    loadingModalState: 1,
                });
                setTimeout(() => {this.reloadData();}, 200);
            },
            (error) => {
                Logger(FILE_NAME, "callCreateUserDefinedDataTypeApi", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    //default
    render() {
        //default
        Logger(FILE_NAME, "render", "", "this.state", this.state);

        return (            
                this.state.isLoaded ? 
                <div>
                    <h1>UserDefinedDataType &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 
                        <CButton color="success" className="px-4" onClick={() => this.reloadData()}>Reload Data</CButton>
                    </h1>
                    <br />
                    {
                    this.state.isLoaded ?
                        <MaterialReactTable
                            initialState={{ density: 'compact' }}
                            columns={tableColumnData} 
                            data={this.state.userDefinedDataTypeListData}
                            enablePagination = {true}

                            editingMode={"row"}
                            enableEditing
                            onEditingRowSave={(exitEditingMode) => this.handleSaveRowEdits(exitEditingMode)}
                        /> : <br/>
                    }
                    <br /><br />
                    {
                        /* Create new UserDefinedDataType */
                        <CContainer>
                        <CRow className="justify-content-center">
                        <CCol md="8">
                            <CCardGroup>
                            <CCard className="p-4">
                                <CCardBody>
                                <CForm onSubmit={this.submitCreateUserDefinedDataTypeForm}>
                                    <h1>Save a new UserDefinedDataType</h1>
                                    <CInputGroup className="mb-3">
                                        <CInputGroupPrepend>
                                            <CInputGroupText>
                                            <CIcon name="cilPencil" />
                                            </CInputGroupText>
                                        </CInputGroupPrepend>
                                        <CInput
                                            placeholder="Name (all CAPS)" 
                                            autoComplete="on"
                                            type="text"
                                            step="any"
                                            id="inputName"
                                            label="UserDefinedDataType Name (all CAPS)"
                                            required
                                        />
                                    </CInputGroup>

                                    <CRow>
                                        <CCol xs="6">
                                            <CButton color="success" className="px-4" type='submit'>SAVE</CButton>
                                        </CCol>
                                        <CCol xs="6" className="text-right">
                                            <CButton color="danger" className="px-0" onClick={() => this.reloadData()}>&nbsp;Reset values&nbsp;</CButton>
                                        </CCol>
                                    </CRow>
                                </CForm>
                                </CCardBody>
                            </CCard>
                            </CCardGroup>
                        </CCol>
                        </CRow>
                        </CContainer>      
                    }

                    {/* //default */}
                    <StatusModal visible={this.state.loadingModalVisible} loadingModalState={this.state.loadingModalState} />
                    <FailureModal visible={this.state.failureModalVisible} />
                </div>: <Loading />
            );
    }

}

export default LoggerUserDefinedDataType;
